body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(209, 212, 215, 0.822);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  position: relative;
}

.page-content {
  position: relative;
  top: 0;
  height: 400px;
  z-index: 100;
  background-color: rgba(255, 255, 255, 0.7);
}

/* alert部分 */
.alter-text {
  position: absolute;
  left: 2%;
  top: 10%;
  color: rgba(53, 108, 39);
  font-size: 1.7rem;
  font-weight: 500;
}

.hr-line {
  position: absolute;
  top: 25%;
  width: 100%;
}

/* svg部分 */
svg.user-icon {
  position: absolute;
  left: 3%;
  top: 34%;
  width: 5%;
  height: 5% ;
}

.users-icon {
  position: absolute;
  left: 3%;
  top: 47%;
  width: 4.5%;
  height: 4.5% ;
}

svg.time-icon {
  position: absolute;
  left: 2.3%;
  top: 60%;
  width: 6%;
  height: 6%;
}

/* entryTitle部分 */
.entry-title {
  font-size: 1.1rem;
}

.title-user-name {
  position: absolute;
  left: 10%;
  top: 33%;
}

.title-assign-type {
  position: absolute;
  left: 10%;
  top: 46%;
}

.title-assign-time {
  position: absolute;
  left: 10%;
  top: 59%;
}

/* EntryText部分 */

.text-user-name {
  position: absolute;
  right: 3%;
  top: 32%;
  font-size: 1.6rem;
  font-weight: 500;
}

.text-assign-type {
  position: absolute;
  right: 3%;
  top: 45%;
  font-size: 1.6rem;
  font-weight: 500;
  color: rgb(208, 124, 51);
}

.text-assign-time {
  position: absolute;
  right: 3%;
  top: 59.5%;
  font-size: 1.2rem;
  font-weight: 500;
  color: rgb(76, 167, 145);
}